<template>
<div class="star" :class="'star-'+size">
    <span class="star-item" :class="item"  v-for="item in num"> </span>
</div>
</template>
<script>
export default{	
	props:['score','size'],
	//星星的个数由计算属性得来的computed:
	computed:{
//定义一个num函数，存放星星个总数
            num(){
                 //定义一个数组 存放星星 数
                 let arr=[]
                 let zhengshu=Math.floor(this.score)                 
                 let xiaoshu=(this.score-zhengshu)>=0.5
                     
                 for(let i=0;i<zhengshu;i++){
                       arr.push('on')
                 }
                 if(xiaoshu){
                       arr.push('half')
                 }
                 while(arr.length<5){

                 arr.push("off")
                 }
                 return arr
            }
	}
}

</script>



