<template>
  <div id="app">   
     <vheader :seller="SandG.seller"></vheader>
     <div class="tab">
          <div class="tab-item">
                  <router-link to="/goods">商品</router-link>
           </div>
           <div class="tab-item">       
                  <router-link to="/ratings">评价</router-link>
           </div>
           <div class="tab-item">      
                  <router-link to="/seller">商家</router-link>
          </div>
     </div>
     <keep-alive>
        <router-view :seller="SandG.seller"></router-view>
     </keep-alive>

  </div>

</template>
<script>
import vheader from './components/vheader.vue'
import axios from 'axios'
export default {
  name:'app',
  data(){
    return{
       SandG:{
        seller:{}
        
       }
    }
  },
  mounted(){
            let self=this
           axios.get("./static/json/data.json").then(function(res){
                       self.SandG.seller=res.data.seller
                      // self.SandG.goods=res.data.goods
                      
           })
     document.addEventListener('visibilitychange', this.changeTitle, false)
  },
    methods: {
      changeTitle () {
        if (document.hidden) {
          document.title = 'you leave'
        } else {
          document.title = 'Lisa`s vue project'
        }
      }
    },
  components:{
  vheader:vheader
  }
  
}
</script>

<style>

.tab .tab-item .router-link-active{color:red;}

</style>





