<template>
<div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
         <ul>
	        <li class="menu-item" v-for="Lmenu in goods">
	              <span class="text">
	                    <span v-show="Lmenu.type>0" class="iconMap" :class="icon[Lmenu.type]"></span>
	                    {{Lmenu.name}}
	              </span>                  
	        </li>
        </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
         <ul>
              <li v-for="FList in goods" class="food-list food-list-hook">
                   <h1 class="title">
                        {{FList.name}}
                   </h1>
                   <ul>
                       <li class="food-item" v-for=" vfood in FList.foods" @click="Detail(vfood)">
                           <div class="icon">
                                <img width="57" height="57" alt="vfood" :src="vfood.icon" />
                           </div>
                           <div class="content">
                                <h2 class="name">{{vfood.name}}</h2>
                                <p class="description">{{vfood.description}}</p>
                                <div class="sell-info">
                                     <span class="sellCount">
                                           月售{{vfood.sellCount}}份
                                     </span>
                                     <span class="rating">
                                           好评率{{vfood.rating}}
                                     </span>
                                     <div class="price">
                                          <span class="newPrice">${{vfood.price}}</span>                           
                                     </div>
                                     <div class="cartcontrol-wrapper">
                                            <addAndsubtract :vfood="vfood"></addAndsubtract>
                                     </div>
                                </div>
                           </div>
                       </li>
                   </ul>
              </li>           
         </ul>
    </div>
    <!--购物车-->
    <shopCar :SelectFoods="SelectFoods" :minPrice="seller.minPrice" :deliveryPrice="seller.deliveryPrice"></shopCar>
    <!--详情页-->
    <goodsDetail :vfood="nowGoods" ref="goodsDetail"></goodsDetail>
</div>
</template>

<script>
import Scroll from 'better-scroll'
import icon from '../../static/js/icon.js'
import axios from 'axios'
import shopCar from './shopCar.vue'
import addAndsubtract from './addAndsubtract.vue'
import goodsDetail from './goodsDetail.vue'
export default{
    props:['seller'],
	data(){
           return{
                icon:icon,
                goods:[],
                nowGoods:''
                
           }
	},
    mounted(){
           axios.get('./static/json/data.json').then(res=>{
                   this.goods=res.data.goods
                  // console.log(this.goods)
                  this.$nextTick(function(){
                       this.scroll()
                  })

           })
    },
	methods:{
	//创建滑动方法
        scroll(){
             /* eslint-disable no-new */
             new Scroll(this.$refs['foodsWrapper'],{click:true})
             new Scroll(this.$refs['menuWrapper'],{click:true})
        },
        Detail(vfood){
            this.nowGoods=vfood
            
            
            this.$refs['goodsDetail'].show()
            console.log(this.nowGoods)

        }
	},
	components:{
	     shopCar,
	     addAndsubtract,
       goodsDetail
	},
	//用实时计算属性 得出 count

    computed:{
         SelectFoods(){
                  let SFoods=[]
                  this.goods.forEach(function(good){
                       good.foods.forEach(function(food){
                              if(food.count>0){
                                    SFoods.push(food)
                              }
                       })
                  })
                 return SFoods
           }


    }
}

</script>


