<template>
 <transition name="move">
	<div class="detailWrapper" v-show="showDetail" ref="detailWrapper">
	     <div class="foodDetail">
             <div class="back" @click="show()">
                 <i class="icon-arrow_lift"></i>
             </div>
             <img :src="vfood.image" height="325" width="100%">
             <div class="info">
                  <div class="title">{{vfood.name}}</div>             
	             <div class="desc">
			            <span>月售{{vfood.sellCount}}</span>
			            <span>好评率{{vfood.rating}}%</span>
	             </div>
	             <div class="price">
			            <span class="unit">现价${{vfood.price}}</span>
			            <span class="oldPrice">原价${{vfood.oldPrice}}</span>
	             </div>
	             <transition name="fade">
		             <div class="shopCart" v-show="vfood.count === 0 || vfood.count==undefined " >
		                  <div class="text" @click="addCar()">加入购物车</div>
		             </div>
	             </transition>
	              <addAndsubtract ref="addAndsubtract" :vfood="vfood"></addAndsubtract>
	          </div>
		     <div class="divider"></div>
		     <div class="desc">
		          <div class="title">商品介绍</div>
		          <div class="content">{{vfood.info}}</div>
	         </div>
	        <div class="divider"></div>
	        <div class="evaluation">
		          <div class="title">
		               商品评价
		          </div>
		          <div class="classify">
                        <span class="item" v-for="(item,index) in classify"
                        :class="{'active':item.active,'bad':index==2,'badActive':index==2&&item.active}" @click="classifyClick(item)">
                          {{item.name}}
                          <span class="count">{{item.count}}</span>
                        </span>
		          </div>
		          <div class="switch" @click='evelflag=!evelflag'>
			            <span class="icon-check_circle" :class="{'on':evelflag}"></span>
			            <span class="text">只看有内容的评价</span>
                  </div>
                  <div class="evel-list">
                       <ul>
                           <li class="evel" v-for="evel in commontS">
                               <div class="userInfo">
                                   <div class="time">{{evel.rateTime}}</div>
                                   <div class="user">
                                         <span>{{evel.username}}</span>
                                         <span class="avatar">
                                         <img :src="evel.avatar" width="12" height="12">
                                         </span>
                                    </div>
                               </div>    
                               <div class="content">
                                 <span class="icon" :class="evel.rateType?'icon-thumb_down':'icon-thumb_up'"></span>
                                 <span class="text">{{evel.text}}</span>
                               </div>

                           </li>
                       </ul>
                  </div>


           </div>
        </div>
	</div>
 </transition>
</template>
<script>

import addAndsubtract from './addAndsubtract.vue'
import Scroll from 'better-scroll'
export default{
        props:['vfood'],

	 data(){
	   return{
	      showDetail:false,
	      classify:[
              {name:'全部',count:0,commont:[],active:true},
              {name:'推荐',count:0,commont:[],active:false},
              {name:'吐槽',count:0,commont:[],active:false}
	      ],
	      evelflag:false,
	      commontAll:[]
         
	   }
	 },
	 methods:{

	     show(){
                this.showDetail=!this.showDetail
                 this.$nextTick(function(){
	                 /* eslint-disable no-new */
	                this.sc= new Scroll(this.$refs['detailWrapper'],{click:true})
                   
                   //console.log(this.vfood.ratings) 
                   this.commontAll=this.vfood.ratings
                   //console.log(this.commontAll) 
                   this.vfood.ratings.forEach(val=>{
                         this.classify[0].count++
                         this.classify[0].commont.push(val)
                         
                         if(val.rateType){
                            this.classify[2].count++
                            this.classify[2].commont.push(val)
                            
                         }else{
                            this.classify[1].count++
                            this.classify[1].commont.push(val)
                          
                         }
                   }) 
                        
	               })

	     },
	     addCar(){
          
               this.$refs['addAndsubtract'].add()  
	     },
	     classifyClick(item){
                 this.commontAll=item.commont
                 this.classify.forEach(val=>{
                      val.active = false
                 })
                       item.active = true
           }
	     
	 },
	 components:{
	        addAndsubtract
	 },
	 computed:{

	     commontS(){
                let arr=[]
               
                if(this.evelflag){
                    this.commontAll.forEach(function(val){                        
                          if(val.text.length>0){
                             arr.push(val)
                          }                         
                    })
                     return arr
                     
                }else{
                     return this.commontAll
                     }
	     }
	 }

}


</script>


