export default{
  // 默认第一个接收的参数是 vuex里所定义的数据，在此例中指state.js里的数据 即vxfood:[]
  vxadd(state,val){
   //console.log(arguments)
   state.vxfood.push(val)
   console.log(state.vxfood)
  },

  vxreduce(state,val){
    state.vxfood.forEach(function(res,index){
            if(res.name==val.name){
              state.vxfood.splice(index,1)
            }
    })

    console.log(state.vxfood)
  }


}




