<template>
<div class="cartcontrol">
    <transition name="fadeRotate">
	     <div class="cart-decrease" v-show="vfood.count>0" @click.stop.prevent="subtract($event)">
	          <span class="icon-remove_circle_outline inner"></span>
	     </div>
     </transition>
     <div class="cart-count" v-show="vfood.count>0">
         {{vfood.count}}
     </div>
     <div id="add" class="cart-add" @click.stop.prevent="add($event)">
          <i class="icon-add_circle"></i>
     </div>

</div>
</template>
<script>
import Vue from 'vue'
import {mapState,mapMutations} from 'vuex'


export default{
	
props:['vfood'],

//在点击加减按钮时，要触发 加减方法
methods:{
      //当点击加号时

...mapMutations([
       'vxadd',
       'vxreduce'
]),

     add(event){
        //count 属性在vfood里没有，在点击加号时使用Vue全局函数 强制给vfood添加 count 属性 


        if(!event._constructed){

             return
        }

        if(typeof this.vfood.count==='undefined'){
                 Vue.set(this.vfood,'count',0)  
                 Vue.set(this.vfood,'active',true) 
                  this.vfood.count=0 
                  this.vfood.active=true
                               
        }        
           this.vfood.count++ 
           if(this.vfood.active){
               this.vxadd(this.vfood)
               this.vfood.active=false
           }
        
         
             
     },
     subtract(event){
         if(!event._constructed){

             return
        }

        this.vfood.count--
        if(this.vfood.count===0){
              this.vxreduce(this.vfood)
              this.vfood.active=true
        }
        

     }     

	
},
computed:{
    
    ...mapState([

           'vxfood'
    ])
}



}


</script>


