<template>
<div class="ratingsWrapper" ref="ratingsWrapper">
    <div class="ratings-content">
        <div class="info">
            <div class="mark">
               <div class="num">{{seller.score}}</div>
               <div class="text">综合评分</div>
               <div class="contrast">高于周边商家{{seller.rankRate}}%</div>
            </div>
            <div class="stars">
                 <div class="serviceScore">
                      <span class="text">服务态度</span>
                      <star :size="36" :score="seller.serviceScore"></star>
                      <span class="num">{{seller.serviceScore}}</span>
                 </div>            
                 <div class="serviceScore">
                      <span class="text">商家评分</span>
                      <star :size="36" :score="seller.foodScore"></star>
                      <span class="num">{{seller.foodScore}}</span>
                 </div>
                 <div class="deliveryTime">
                       <span class="text">送达时间</span>
                       <span class="time">{{seller.deliveryTime}}</span>
                 </div>
            </div>  
        </div>
        <div class="divider"></div>
        <div class="evaluation">
            <div class="classify">
                  <span class="item" v-for="(item,index) in classifyArr" :class="{'active':item.active,'bad':index==2,'badActive':index==2&&item.active}" @click="select(item)">
                        {{item.name}}
                        <span class="count">{{item.count}}</span>
                   </span>
            </div>
            <div class="switch" @click="check=!check">
                  <span class="icon-check_circle" :class="{'on':check}"></span>
                  <span class="text">只看有内容的评价</span>
            </div>
            <div class="evel-list">
                  <ul>
                    <li class="evel" v-for="every in checkFlag">
                      <div class="avatar">
                        <img :src="every.avatar" width="28" height="28">
                      </div>
                      <div class="content">
                        <div class="user">
                          <span class="name">{{every.username}}</span>
                          <span class="rateTime">{{every.rateTime}}</span>
                        </div>
                        <div class="star-wrapper">
                          <star :size="24" :score="every.score"></star>
                          <span class="deliveryTime">{{every.deliveryTime}}分钟送达</span>
                        </div>
                        <div class="text">
                               {{every.text}}
                        </div>
                        <div class="recommend">
                          <span class="icon" :class="every.rateType?'icon-thumb_down':'icon-thumb_up'"></span>
                          <span class="dish" v-for="dish in every.recommend">{{dish}}</span>
                        </div>
                      </div>
                    </li>
                  </ul>
           </div>
        </div>  

    </div>
</div>
</template>
<script>
import star from './star.vue'
import axios from 'axios'
import Scroll from 'better-scroll'

export default{
    props:['seller'],

    data(){
          return{

             ratings:[],
             classifyArr:[
                    {name:'全部',count:0,active:true,commont:[]},
                    {name:'推荐',count:0,active:false,commont:[]},
                    {name:'吐槽',count:0,active:false,commont:[]}
             ],
             check:false,
             allCommont:[]
          }
    },
	
	mounted(){
	     axios.get('./static/json/data.json').then(res=>{         
             this.ratings=res.data.ratings
            // console.log(this.ratings)
             this.allCommont=this.ratings
             this.allCommont.forEach(val=>{
              this.classifyArr[0].count++
                this.classifyArr[0].commont.push(val)   
                 if(val.rateType){
                    this.classifyArr[2].count++
                    this.classifyArr[2].commont.push(val)
                 }else{
                     this.classifyArr[1].count++
                    this.classifyArr[1].commont.push(val)
                 }

             })
             this.$nextTick(function(){
              /* eslint-disable no-new */
             this.sc= new Scroll(this.$refs['ratingsWrapper'],{click:true})
               })

         })

         
                

        
	},
  methods:{
           select(item){
                    this.allCommont=item.commont
                 this.classifyArr.forEach(val=>{
                       val.active=false
                 })
                 item.active=true
           },
           updateS(){

                  this.$nextTick(function(){

                      this.sc.refresh()
                  })
           }

         
  },
	components:{
	         star
	},
  computed:{
           checkFlag(){
                    this.updateS()
                if(this.check){
                   let arr=[]
                      this.allCommont.forEach(function(val){
                          if(val.text.length>0){                               
                               arr.push(val)
                          }
                      })
                      return arr
                }else{
                   return this.allCommont
                }
                     
           }

  },
  activated(){
  }
}
</script>


