<template>
<div class="shopCart">
     <div class="content" @click="showA">
          <div class="content-left">
               <div class="logo-wrapper">
                    <div class="logo" :class="{'active':totalPrice>0}">
                         <i class="icon-shopping_cart"></i>
                    </div>
                    <div class="badge" v-show="totalCount>0">{{totalCount}}</div>
               </div>
	           <div class="price" :class="{'active':totalPrice>0}" v-show="totalPrice>0" >{{totalPrice}}</div>
               <div class="desc"  v-show="totalPrice<100">另需要配送费{{deliveryPrice}}元</div>
          </div>
          <div class="content-right" :class="{'enough':totalPrice>=minPrice}">
               <div class="pay">{{PayMoney}}</div>
          </div>
     </div>
     <div class="shopcart-list" v-show="show && totalPrice>0">
        <div class="list-header">
          <h1 class="title">购物车</h1>
          <span class="empty" @click="empty">清空</span>
        </div>
        <transition name="transHeight" >
	        <div class="list-content" ref="foodlist">
		          <ul>
			          <li class="food" v-for="list in SelectFoods">
			              <span class="name">{{list.name}}</span>
			              <div class="price">
	                            <span>{{list.price*list.count}}</span>
	                      </div>
	                      <div class="cartcontrol-wrapper">
	                          <addAndsubtract :vfood="list"></addAndsubtract>
	                          <!--vfood需要接收当前所点击的商品数据，所以需要把当前商品传递过来-->
	                      </div>
			          </li>
			      </ul> 
		    </div>     
        </transition>
     </div>     

</div>
</template>

<script>
import addAndsubtract from './addAndsubtract.vue'
import Scroll from 'better-scroll'

export default{
   props:['SelectFoods','minPrice','deliveryPrice'],
   data(){
        return{
           show:false
        }
   },
   components:{
	   
	     addAndsubtract
	},
   //实时计算属性 得出总价  数量等
   computed:{
           //总价
           totalPrice(){
                 let totalP=0
                 this.SelectFoods.forEach(function(val){
                           totalP+=val.price*val.count
                 })
                  return totalP

           }, 
           //选择商品的总数量          
            totalCount () {
			        let count = 0			      
			        this.SelectFoods.forEach(food => {			         
			          count=count+food.count
			        })
			        return count
                },
            //起送价    结算
            PayMoney(){

                      if(this.totalPrice===0){
                          return `${this.minPrice}起送`
                      }else if(this.totalPrice<this.minPrice){
                             return `还差${this.minPrice-this.totalPrice}元起送`
                      }else{
                            return `去结算`
                      }

            }

   },
   methods:{
          empty(){
               this.show=false
               this.SelectFoods.forEach(food => {			         
			          food.count=0
			        })
           },
            showA(){
                 this.show=!this.show

                 this.$nextTick(function(){
 
		                  this.$nextTick(function(){
			                 if(!this.sc){
			                 this.sc=new Scroll(this.$refs['foodlist'],{click:true})
			                 }else {
			                 //强制重新计算高度
			                 this.sc.refresh()
			                      }                     
		                   })


                    })
                 

              }
   }	
     

}

</script>


